/* hash: hylit */
.side-panel__appear_1103s,
.side-panel__enter_1103s {
    opacity: 0;
    transform: translateY(15px);
}

.side-panel__appearActive_1103s,
.side-panel__enterActive_1103s {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.side-panel__exit_1103s {
    opacity: 1;
    transform: translateY(0);
}

.side-panel__exitActive_1103s,
.side-panel__exitDone_1103s {
    opacity: 0;
    transform: translateY(15px);
    transition: opacity 200ms ease-out, transform 200ms ease-out;
}
